import http from '@/api/http.js'
export default{
	data(){
		return{
			list:[]
		}
	},
	onLoad() {
		this.getList()
	},
	methods:{
		getList(){
			http('/api/Setting/getUseHelp','post').then(res=>{
				this.list = res.data.data
				console.log(res)
			})
		},
		toHelp(id){
			uni.navigateTo({
				url:'/pages/mine/set/helpDetail?id='+id
			})
		}
	}
}
